<script>
export default {
  props: {
    load: {
      type: Function,
      default: () => {}
    },
    mod: ''
  },
  data() {
    return {
      visible: true,
    }
  },
  created (){
    const load = this.$route.meta.comp
    load().then(m => {
      this.m = m
      // console.log(m);
      this.visible = false
    })
  },
  render(h) {
    const comp = this.visible ? 'za-loading' : this.m
    return h(comp, {
      props: {
        visible: this.visible
      }
    });
  },
}
</script>



