import Vue from 'vue'
import Router from 'vue-router'
import bundle from './bundle';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: () => import('../components/index.vue')
    },
    {
      path: '/input',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/inputPage.vue')
      }
    },
    {
      path: '/checkbox',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/checkboxPage.vue')
      }
    },
    {
      path: '/icon',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/iconPage.vue')
      }
    },
    {
      path: '/button',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/buttonPage.vue')
      }
    },
    {
      path: '/spinner',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/spinnerPage.vue')
      }
    },
    {
      path: '/cell',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/cellPage.vue')
      }
    },
    {
      path: '/message',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/messagePage.vue')
      }
    },
    {
      path: '/radio',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/radioPage.vue')
      }
    },
    {
      path: '/badge',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/badgePage.vue')
      }
    },
    {
      path: '/tabs',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/tabsPage.vue')
      }
    },
    {
      path: '/stepper',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/stepperPage.vue')
      }
    },
    {
      path: '/switch',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/switchPage.vue')
      }
    },
    {
      path: '/toast',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/toastPage.vue')
      }
    },
    {
      path: '/popup',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/popupPage.vue')
      }
    },
    {
      path: '/uploader',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/uploaderPage.vue')
      }
    },
    {
      path: '/actionsheet',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/actionsheetPage.vue')
      }
    },
    {
      path: '/modal',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/modalPage.vue')
      }
    },
    {
      path: '/swipeaction',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/swipeactionPage.vue')
      }
    },
    {
      path: '/swipe',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/swipePage.vue')
      }
    },
    {
      path: '/picker',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/pickerPage.vue')
      }
    },
    {
      path: '/progress',
      component: () => import('./bundle.vue'),
      meta: {
        comp: () => import('../components/progressPage.vue')
      }
    }
  ]
})



